import React from 'react';
import './Services.css';
import ServicesSlider from '../ServicesSlider/ServicesSlider';
import ServiceCard from '../ServiceCard/ServiceCard';
import {
    BrowserRouter as Router,
    Switch,
    Redirect,
    Route,
    Link,
    useRouteMatch
  } from "react-router-dom";

const Services = () => {
    let match = useRouteMatch();
    return(
        <section>
            <div className="banner">
                <p className="heroText">Our Services</p>
            </div>
            <div className="cardContainer">
                <ServicesSlider cl="sContainer">
                    <ServiceCard heading="Criminal Matters" to="criminalMatters"/>
                    <ServiceCard heading="Civil Matters" to="civilMatters"/>
                    <ServiceCard heading="Family Matters" to="familyMatters"/>
                    <ServiceCard heading="Constitutional Matters" to="constitutionalMatters"/>
                    <ServiceCard heading="Service Matters" to="serviceMatters"/>
                    <ServiceCard heading="NRI Matters" to="nriMatters"/>
                    <ServiceCard heading="Consumer Matters" to="consumerMatters"/>
                    <ServiceCard heading="Taxation Matters" to="taxationMatters"/>
                    <ServiceCard heading="Real Estate Matters" to="realEstateMatters"/>
                    <ServiceCard heading="Arbitration Services" to="arbitrationServices"/>
                    <ServiceCard heading="Alternative Dispute Resolution" to="alternativeDisputeResolution"/>
                    <ServiceCard heading="Legal Drafting" to="legalDrafting"/>
                </ServicesSlider>
            </div>
        </section>
    )
}

export default Services;