import React from 'react';
import './ServiceCard.css';
import {
    Link
  } from "react-router-dom";

const ServiceCard = ({heading, to}) => {
    return(
        <Link style={{ color: 'inherit',textDecoration: 'inherit' }}  to={"services/"+to}>
            <div className="serviceCard">
                <p>{heading}</p>
            </div>
        </Link>
    )
}

export default ServiceCard;