import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Redirect,
  Route,
  Link,
  useRouteMatch
} from "react-router-dom";
import ServicesPage from '../ServicesPage/ServicesPage';
import "./ServiceMenu.css";

const ServicesMenu = () => {
  let match = useRouteMatch();

  return (
    <div className="marTop">
      <div className="banner">
          <p>Services</p>
      </div>
      <div className="paddingBox">
        <ul className="serviceMenuList">
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }}  to={`${match.url}/criminalMatters`}>Criminal Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }}  to={`${match.url}/civilMatters`}>Civil Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }}  to={`${match.url}/familyMatters`}>Family Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }}  to={`${match.url}/constitutionalMatters`}>Constitutional Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }}  to={`${match.url}/serviceMatters`}>Service Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to={`${match.url}/nriMatters`}>NRI Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to={`${match.url}/consumerMatters`}>Consumer Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to={`${match.url}/taxationMatters`}>Taxation Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to={`${match.url}/realEstateMatters`}>Real Estate Matters</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to={`${match.url}/arbitrationServices`}>Arbitration Services</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to={`${match.url}/alternativeDisputeResolution`}>Alternative Dispute Resolution</Link></li>
          <li><Link style={{ color: 'inherit',textDecoration: 'inherit' }} to={`${match.url}/legalDrafting`}>Legal Drafting</Link></li>
        </ul>
        <Switch>
          <Route path={`${match.path}/:topicId`}>
            <ServicesPage />
          </Route>
          <Route path={match.path}>
            <Redirect to={`${match.path}/criminalMatters`} />
          </Route>
        </Switch>
      </div>
    </div>
  );
}

export default ServicesMenu;
