import React from 'react';
import "./JoinUs.css";
import JotformEmbed from 'react-jotform-embed';

const JoinUs = () => {
    return(
        <section className="marTop">
            <div className="banner">
                <p>Work with us</p>
            </div>
            <div className="containerBox" style={{"position": "relative"}}>      
                <JotformEmbed src="https://form.jotform.com/203111987670052" />
                <div className="hidebar"></div>
            </div>
        </section>
    )
}

export default JoinUs;
