        <p className="pText">Many families in India invest in real estate – seeing property as a stable and secure investment for the long term. However, transactions related to property not only require a lot of time and paperwork – they also require an understanding of the laws, rules, regulations, in respect of selling, buying, renting or using the same. Without this, it can be difficult to secure your investment – or even understand exactly what you have invested in.</p>
